import React from "react";
import type { IBounds } from "react-laag";
import { useLayer } from "react-laag";
import { DataEditor } from "../../data-editor/data-editor";
import {
    BeautifulWrapper,
    Description,
    PropName,
    useMockDataGenerator,
    defaultProps,
} from "../../data-editor/stories/utils";
import type { GridMouseEventArgs } from "../../data-grid/data-grid-types";
import { SimpleThemeWrapper } from "../../stories/story-utils";

export default {parameters: {"storySource":{"source":"import React from \"react\";\nimport type { IBounds } from \"react-laag\";\nimport { useLayer } from \"react-laag\";\nimport { DataEditor } from \"../../data-editor/data-editor\";\nimport {\n    BeautifulWrapper,\n    Description,\n    PropName,\n    useMockDataGenerator,\n    defaultProps,\n} from \"../../data-editor/stories/utils\";\nimport type { GridMouseEventArgs } from \"../../data-grid/data-grid-types\";\nimport { SimpleThemeWrapper } from \"../../stories/story-utils\";\n\nexport default {\n    title: \"Glide-Data-Grid/DataEditor Demos\",\n\n    decorators: [\n        (Story: React.ComponentType) => (\n            <SimpleThemeWrapper>\n                <BeautifulWrapper\n                    title=\"Tooltips\"\n                    className=\"double\"\n                    description={\n                        <Description>\n                            Using the <PropName>onItemHovered</PropName> event makes it easy to create tooltips. This\n                            story is intentionally forced to scroll vertically so layout in scrolling documents can be\n                            confirmed.\n                        </Description>\n                    }>\n                    <Story />\n                </BeautifulWrapper>\n            </SimpleThemeWrapper>\n        ),\n    ],\n};\n\nconst zeroBounds = {\n    left: 0,\n    top: 0,\n    width: 0,\n    height: 0,\n    bottom: 0,\n    right: 0,\n};\n\nexport const Tooltips: React.VFC = () => {\n    const { cols, getCellContent } = useMockDataGenerator(6);\n\n    const [tooltip, setTooltip] = React.useState<{ val: string; bounds: IBounds } | undefined>();\n\n    const timeoutRef = React.useRef(0);\n\n    const onItemHovered = React.useCallback((args: GridMouseEventArgs) => {\n        if (args.kind === \"cell\") {\n            window.clearTimeout(timeoutRef.current);\n            setTooltip(undefined);\n            timeoutRef.current = window.setTimeout(() => {\n                setTooltip({\n                    val: `Tooltip for ${args.location[0]}, ${args.location[1]}`,\n                    bounds: {\n                        // translate to react-laag types\n                        left: args.bounds.x,\n                        top: args.bounds.y,\n                        width: args.bounds.width,\n                        height: args.bounds.height,\n                        right: args.bounds.x + args.bounds.width,\n                        bottom: args.bounds.y + args.bounds.height,\n                    },\n                });\n            }, 1000);\n        } else {\n            window.clearTimeout(timeoutRef.current);\n            timeoutRef.current = 0;\n            setTooltip(undefined);\n        }\n    }, []);\n\n    React.useEffect(() => () => window.clearTimeout(timeoutRef.current), []);\n\n    const isOpen = tooltip !== undefined;\n    const { renderLayer, layerProps } = useLayer({\n        isOpen,\n        triggerOffset: 4,\n        auto: true,\n        container: \"portal\",\n        trigger: {\n            getBounds: () => tooltip?.bounds ?? zeroBounds,\n        },\n    });\n\n    return (\n        <>\n            <DataEditor\n                {...defaultProps}\n                onItemHovered={onItemHovered}\n                getCellContent={getCellContent}\n                columns={cols}\n                rows={1000}\n            />\n            {isOpen &&\n                renderLayer(\n                    <div\n                        {...layerProps}\n                        style={{\n                            ...layerProps.style,\n                            padding: \"8px 12px\",\n                            color: \"white\",\n                            font: \"500 13px Inter\",\n                            backgroundColor: \"rgba(0, 0, 0, 0.85)\",\n                            borderRadius: 9,\n                        }}>\n                        {tooltip.val}\n                    </div>\n                )}\n        </>\n    );\n};\n","locationsMap":{"tooltips":{"startLoc":{"col":35,"line":47},"endLoc":{"col":1,"line":118},"startBody":{"col":35,"line":47},"endBody":{"col":1,"line":118}}}},},
    title: "Glide-Data-Grid/DataEditor Demos",

    decorators: [
        (Story: React.ComponentType) => (
            <SimpleThemeWrapper>
                <BeautifulWrapper
                    title="Tooltips"
                    className="double"
                    description={
                        <Description>
                            Using the <PropName>onItemHovered</PropName> event makes it easy to create tooltips. This
                            story is intentionally forced to scroll vertically so layout in scrolling documents can be
                            confirmed.
                        </Description>
                    }>
                    <Story />
                </BeautifulWrapper>
            </SimpleThemeWrapper>
        ),
    ],
};

const zeroBounds = {
    left: 0,
    top: 0,
    width: 0,
    height: 0,
    bottom: 0,
    right: 0,
};

export const Tooltips: React.VFC = () => {
    const { cols, getCellContent } = useMockDataGenerator(6);

    const [tooltip, setTooltip] = React.useState<{ val: string; bounds: IBounds } | undefined>();

    const timeoutRef = React.useRef(0);

    const onItemHovered = React.useCallback((args: GridMouseEventArgs) => {
        if (args.kind === "cell") {
            window.clearTimeout(timeoutRef.current);
            setTooltip(undefined);
            timeoutRef.current = window.setTimeout(() => {
                setTooltip({
                    val: `Tooltip for ${args.location[0]}, ${args.location[1]}`,
                    bounds: {
                        // translate to react-laag types
                        left: args.bounds.x,
                        top: args.bounds.y,
                        width: args.bounds.width,
                        height: args.bounds.height,
                        right: args.bounds.x + args.bounds.width,
                        bottom: args.bounds.y + args.bounds.height,
                    },
                });
            }, 1000);
        } else {
            window.clearTimeout(timeoutRef.current);
            timeoutRef.current = 0;
            setTooltip(undefined);
        }
    }, []);

    React.useEffect(() => () => window.clearTimeout(timeoutRef.current), []);

    const isOpen = tooltip !== undefined;
    const { renderLayer, layerProps } = useLayer({
        isOpen,
        triggerOffset: 4,
        auto: true,
        container: "portal",
        trigger: {
            getBounds: () => tooltip?.bounds ?? zeroBounds,
        },
    });

    return (
        <>
            <DataEditor
                {...defaultProps}
                onItemHovered={onItemHovered}
                getCellContent={getCellContent}
                columns={cols}
                rows={1000}
            />
            {isOpen &&
                renderLayer(
                    <div
                        {...layerProps}
                        style={{
                            ...layerProps.style,
                            padding: "8px 12px",
                            color: "white",
                            font: "500 13px Inter",
                            backgroundColor: "rgba(0, 0, 0, 0.85)",
                            borderRadius: 9,
                        }}>
                        {tooltip.val}
                    </div>
                )}
        </>
    );
};
