export const OneMillionRows: React.VFC = () => {
    const { cols, getCellContent } = useMockDataGenerator(6);

    const [rows, setRows] = React.useState(1_000_000);

    React.useEffect(() => {
        window.setTimeout(() => setRows(5), 3000);
    }, []);

    return (
        <DataEditor
            {...defaultProps}
            height="100%"
            getCellContent={getCellContent}
            columns={cols}
            rowHeight={31}
            rows={rows}
            rowMarkers="number"
        />
    );
};
